footer {
  background: var(--color-primary);
  padding: 3rem 0;
  text-align: center;
  font-size: 0.9rem;
  margin-top: 7rem;
}

footer a {
  color: var(--color-bg);
}

.footer-logo {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 2rem;
  display: inline-block;
}

.links {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap:2rem;
  margin: 0 auto 3rem;
}

.footer-copyright {
  margin-bottom: 4rem;
  color: var(--color-bg);
}

/* responsive layouts */

@media screen and (max-width: 600px) {
  .links {
    flex-direction: column;
    gap: 1.5rem;
  }
}
